/*
先布局，再定位，zai
*/

.bottom {
    flex-grow: 0;
    flex-shrink: 0;
    position: static;
    bottom: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    pointer-events: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    transition: transform .5s;
    background-color: rgba(0,0,0,.3);
    z-index: 2;
}

.controls {
    position: relative;
    display: flex;
    flex-shrink: 0;
    flex-wrap: nowrap;
    justify-content: space-between;
    height: 37px;
    margin: 10px;
    pointer-events: none;
    opacity: 1;
    transition: opacity .5s;
}

.collapsible-list {
    position: absolute;
    width: 100%;
    display: flex;
    flex-direction: row;
    pointer-events: none;
}

.collapsible-item {
    flex-grow: 1;
    flex-shrink: 1;
    min-width: 0;
}

.collapsible-item > button {
    pointer-events: auto;
}
