.default__user>.gui__top {
    transition: opacity .5s;
    display: flex;
    align-items: center;
    pointer-events: none;
    opacity: 1;
}

.default__user>.gui__top>.gui__top--bar{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    user-select: none;
    transition: opacity .5s;
    color: white;
    font-size: 16px;
    text-shadow: 0 1px 2px rgba(0, 0, 0, .4);
}

.default__user>.gui__overlay {
    display: flex;
    flex-direction: row;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    opacity: .5;
}

.default__user>.gui__overlay>.scene-overlay {
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    flex-grow: 1;
    flex-shrink: 1;
    transition: height .5s;
}

.default__user>.gui__bottom {
    position: absolute;
    padding: 10px 20px;
    box-sizing: border-box;
    flex-grow: 0;
    flex-shrink: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    pointer-events: none;
    user-select: none;
    transition: transform .5s;
}

.default__user>.gui__bottom>.controls {
    position: relative;
    display: flex;
    flex-shrink: 0;
    flex-wrap: nowrap;
    justify-content: space-between;
    height: 37px;
    margin: 10px;
    pointer-events: none;
    opacity: 1;
    transition: opacity .5s;
}

.default__user>.gui__bottom>.controls>.collapsible-list {
    position: absolute;
    width: 100%;
    display: flex;
    flex-direction: row;
    pointer-events: none;
}

.default__user>.gui__bottom>.controls>.collapsible-list>.collapsible-item{
    display: flex;
    justify-content: center;
    flex-grow: 1;
    flex-shrink: 1;
    min-width: 0;
    color: white;
    text-shadow: 1px 1px 2px #000;
}

.default__user>.gui__bottom>.controls>.collapsible-list>.collapsible-item>div {
    pointer-events: auto;
}

.btn {
    cursor: pointer;
    pointer-events: auto;
}
