.realsee-design-toast {
  width: 100%;
  position: fixed;
  z-index: 9999;
  bottom: 4%;
  color: white;
  font-size: 14px;
  display: flex;
  justify-content: center;
  opacity: 1;
  transition: opacity 225ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, transform 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

.realsee-design-toast> .react-toast-contain {
  width: 90%;
  word-break: break-word;
  background: var(--realsee-main-color);
  border-radius: 6px;
  padding: 10px 20px;
  box-sizing: border-box;
  box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, .2), 0px 6px 10px 0px rgba(0 0 0 .14), 0px 1px 18px 0px rgba(0 0 0 .12);
}

.realsee-design-toast.react-toast-error>.react-toast-contain {
  background: #d32f2f;
}

