.vr-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0;
    pointer-events: none;
    animation: fadeOutEase 1.5s cubic-bezier(.16,1,.3,1);
}

.vr-container.main {
    opacity: 1;
    pointer-events: auto;
    animation: fadeInEase 0.5s cubic-bezier(.16,1,.3,1);
}

.vr-container.split {
    height: 50%;
    opacity: 1;
    pointer-events: auto;
    animation: fadeInEase 0.5s cubic-bezier(.16,1,.3,1);
}

.vr-container.main.split {
    top: 50%;
}

@keyframes fadeInEase {
    0% {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fadeOutEase {
    0% {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}
