.realsee-design-barrage {
  width: 100%;
  position: fixed;
  z-index: 9999;
  right: 0;
  color: white;
  text-shadow: 0 1px 2px rgba(0, 0, 0, .4);
  font-size: 12px;
  font-weight: bold;
  transform: translateY(-50%);
  display: flex;
  justify-content: flex-start;
  animation: Trans 10s linear;
}

@media screen and (min-width: 768px) {
  .realsee-design-barrage {
    width: 100%;
    position: fixed;
    z-index: 9999;
    right: 0;
    color: white;
    text-shadow: 0 1px 2px rgba(0, 0, 0, .4);
    font-size: 12px;
    font-weight: bold;
    transform: translateY(-50%);
    display: flex;
    justify-content: flex-start;
    animation: Trans 20s linear;
  }
}

.realsee-design-barrage> .react-barrage-contain {
  width: max-content;
  max-width: 90%;
  word-break: break-word;
  background: rgba(0, 0, 0, .25);
  border-radius: 12px;
  padding: 4px 10px;
  box-sizing: border-box;
}

@keyframes Trans {
  0% {
    opacity: 0;
    transform: translateX(100%);
  }

  25% {
    opacity: 1;
    transform: translateX(50%);
  }

  50% {
    opacity: 1;
    transform: translateX(0);
  }

  75% {
    opacity: 1;
    transform: translateX(-50%);
  }

  100% {
    opacity: 0;
    transform: translateX(-100%);
  }
}
