section {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

main {
  flex: 1;
  position: relative;
  width: 100%;
  background-color: #ccc;
}

#canvas-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

#gui {
  position: absolute;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  pointer-events: none;
  animation: fadeInUp_max 1.5s cubic-bezier(.16,1,.3,1);
}

@media screen and (max-width: 768px) {
  #gui {
    animation: fadeInUp_min 1.5s cubic-bezier(.16,1,.3,1);
  }
}

@keyframes fadeInUp_max {
  0% {
    opacity: 0;
    transform: translateX(-100%)
  }

  to {
    opacity: 1;
    transform: translateX(0)
  }
}

@keyframes fadeInUp_min {
  0% {
    opacity: 0;
    transform: translateY(100%)
  }

  to {
    opacity: 1;
    transform: translateY(0)
  }
}
