.default__live>.gui__top {
    transition: opacity .5s;
    display: flex;
    align-items: center;
    opacity: 1;
}

.default__live>.gui__top>.gui__top--bar{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    transition: opacity .5s;
    color: white;
    font-size: 16px;
}

.default__live>.gui__top>.gui__top--bar>.btn{
    user-select: none;
    cursor: pointer;
    pointer-events: auto;
    text-shadow: 0 1px 2px rgba(0, 0, 0, .4);
}

.default__live>.gui__overlay {
    display: flex;
    flex-direction: row;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
}

.default__live>.gui__overlay>.scene-overlay {
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    flex-grow: 1;
    flex-shrink: 1;
    transition: height .5s;
}

.default__live>.gui__overlay>.scene-overlay>.overlay-modal{
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 40px 20px 20px;
    width: 100%;
    border-radius: 8px 8px 0 0;
    background-color: black;
    box-sizing: border-box;
    z-index: 9;
    animation: transitionUp 1.5s cubic-bezier(.16,1,.3,1);
}

.default__live>.gui__overlay>.scene-overlay>.overlay-modal>.icon-close{
    color: white;
    position: absolute;
    top: 5px;
    right: 20px;
    pointer-events: auto;
}

.default__live>.gui__bottom {
    position: absolute;
    padding: 10px 20px;
    box-sizing: border-box;
    flex-grow: 0;
    flex-shrink: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    pointer-events: none;
    user-select: none;
    transition: transform .5s;
}

.default__live>.gui__bottom>.controls {
    position: relative;
    display: flex;
    flex-shrink: 0;
    flex-wrap: nowrap;
    justify-content: space-between;
    pointer-events: none;
    opacity: 1;
    transition: opacity .5s;
}

.default__live>.gui__bottom>.controls>.collapsible-list {
    position: absolute;
    width: 100%;
    display: flex;
    flex-direction: row;
    pointer-events: none;
}

.default__live>.gui__bottom>.controls>.collapsible-list>.collapsible-item {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-grow: 1;
    flex-shrink: 1;
    min-width: 0;
    color: white;
    text-shadow: 1px 1px 2px #000;
}

.default__live>.gui__bottom>.controls>.collapsible-list>.collapsible-item>div {
    pointer-events: auto;
    text-shadow: 0 1px 2px rgba(0, 0, 0, .4);
}

.default__live>.gui__bottom>.controls>.collapsible-list>.collapsible-item>.members{
    font-size: 10px;
    text-align: justify;
}

@keyframes transitionUp {
    0% {
        opacity: 0;
        transform: translateY(100%);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes transitionDown {
    0% {
        opacity: 1;
        transform: translateY(0);
    }

    to {
        opacity: 0;
        transform: translateY(-100%);
    }
}

.modal__wrap {
    position: absolute;
    width: 80%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    outline: none;
    background-color: white;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
}

.modal__header {
    padding: 0 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
    background-color: #f1f3f4;
    color: black;
}

.modal__body {
    padding: 10px;
    width: 100%;
    box-sizing: border-box;
}

.link-icon-list {
    margin: 10px 0;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}

.link-copy {
    margin: 10px 0;
}

.link-content {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
    box-sizing: border-box;
    overflow: hidden;
}

.link{
    flex: 1;
    background-color: #f1f3f4;
}

.tip{
    font-size: 12px;
    line-height: .5;
}

